const IPFS = require('ipfs-http-client');
const Y = require('yjs');
require('y-memory')(Y);
require('y-array')(Y);
require('y-text')(Y);

const IPFS_OPTIONS = {
  EXPERIMENTAL: {
    pubsub: true,
  },
};

(async () => {
  const ipfs = await IPFS.create(IPFS_OPTIONS);
  const ydoc = new Y.Doc();
  const text = ydoc.getText('text');

  const textArea = document.getElementById('textArea');
  text.observe(event => {
    textArea.value = event.currentTarget.toString();
  });

  // Helper function to set the Yjs document to the content of the textarea
  const updateYjsDocument = () => {
    const content = textArea.value;
    ydoc.transact(() => {
      text.delete(0, text.length);
      text.insert(0, content);
    });
  };

  // Handle user input in the textarea
  textArea.addEventListener('input', () => {
    updateYjsDocument();
  });

  // Handle Yjs changes
  ydoc.on('update', () => {
    updateYjsDocument();
  });

  // Broadcast changes to IPFS peers using pubsub
  const topic = 'yjs-ipfs-text-editor';
  const encoder = new TextEncoder();
  setInterval(() => {
    const content = encoder.encode(JSON.stringify(ydoc.toJSON()));
    ipfs.pubsub.publish(topic, content);
  }, 1000);

  // Subscribe to IPFS pubsub topic and apply changes from other peers
  ipfs.pubsub.subscribe(topic, msg => {
    const data = JSON.parse(new TextDecoder().decode(msg.data));
    ydoc.transact(() => {
      ydoc.getMap('text').clear();
      Y.applyUpdate(ydoc, data);
    });
  });
})();